import firebase from '../firebase.js';
import '../App.scss';
import Header from './Header';
import Product from './Product';
import Popup from './Popup';


class Womens extends Component {

  state = {
    products: [
      {
        type: 'shoes',
        name: 'Thea',
        price: '100',
        imgThumb: '/images/womens/shoes-thea-thumb.jpg',
        imgDetail: '/images/womens/shoes-thea-detail.jpg',
        description: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris.'
      },
      {
        type: 'shoes',
        name: 'Asla',
        price: '150',
        imgThumb: '/images/womens/shoes-asla-thumb.jpg',
        imgDetail: '/images/womens/shoes-asla-detail.jpg',
        description: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris.'
      },
      {
        type: 'shoes',
        name: 'Clara',
        price: '60',
        imgThumb: '/images/womens/shoes-clara-thumb.jpg',
        imgDetail: '/images/womens/shoes-clara-detail.jpg',
        description: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris.'
      },
      {
        type: 'shoes',
        name: 'Adrianna',
        price: '120',
        imgThumb: '/images/womens/shoes-adrianna-thumb.jpg',
        imgDetail: '/images/womens/shoes-adrianna-detail.jpg',
        description: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris.'
      },
      {
        type: 'coat',
        name: 'Aleida',
        price: '120',
        imgThumb: '/images/womens/coat-aleida-thumb.jpg',
        imgDetail: '/images/womens/coat-aleida-detail.jpg',
        description: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris.'
      },
      {
        type: 'coat',
        name: 'Amice',
        price: '50',
        imgThumb: '/images/womens/coat-amice-thumb.jpg',
        imgDetail: '/images/womens/coat-amice-detail.jpg',
        description: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris.'
      },
      {
        type: 'coat',
        name: 'Hay',
        price: '60',
        imgThumb: '/images/womens/coat-hay-thumb.jpg',
        imgDetail: '/images/womens/coat-hay-detail.jpg',
        description: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris.'
      },
      {
        type: 'coat',
        name: 'Nala',
        price: '200',
        imgThumb: '/images/womens/coat-nala-thumb.jpg',
        imgDetail: '/images/womens/coat-nala-detail.jpg',
        description: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris.'
      }

    ],
    filteredProducts: [],
    displayedProducts: [],
    showPopup: false,
    clickedProductName: "",
    clickedProductPrice: ""
  }





  //This recieved data from Product.js
  showPopup = (name, price, imgThumb, imgDetail, description) => {
    this.setState({
      clickedProductName: name,
      clickedProductPrice: price,
      clickedPoductImgThumb: imgThumb,
      clickedPoductImgDetail: imgDetail,
      clickedPoductDescription: description
    })


    if(!this.state.showPopup){
      this.setState({
        showPopup: true
      })
    }
    else {
      this.setState({
        showPopup: false
      })
    }
  }

  hidePopup = () => {
    this.setState({
      showPopup: false
    })
  }


  filterShoes = () => {
    let filtered = this.state.products.filter(product => product.type === 'shoes')
    this.setState({
      displayedProducts: filtered
    })
  }

  removeFilters = () => {
    this.setState({
      displayedProducts: this.state.products
    })
  }

  componentDidMount(){
    this.setState({
      displayedProducts: this.state.products
    })



    const productsDataRef = firebase.database().ref().child('products');

    productsDataRef.on('value', snap => console.log(snap.val()));

    productsDataRef.on('value', (snapshot) => {
      let products = snapshot.val();
      let newState = [];

      console.log(products)
      for (let product in products) {
        newState.push({
          id: product,
          text: product.name
        })
      }

      this.setState({
        allProducts: newState
      })
    })


  }


  render() {


    //Map over current list of products in state and produce a Product component with the data passed in as props
    //TO DO make key unique
    let listOfProducts = this.state.displayedProducts.map((product) =>
        <Product
          key={product}
          imgThumb={product.imgThumb}
          imgDetail={product.imgDetail}
          name={product.name}
          price={product.price}
          description={product.description}
          recieveProductData={this.showPopup}
        />
    )



    let popup;
    if (this.state.showPopup) {
      popup = <Popup
                name={this.state.clickedProductName}
                price={this.state.clickedProductPrice}
                imgThumb={this.state.clickedPoductImgThumb}
                imgDetail={this.state.clickedPoductImgDetail}
                description={this.state.clickedPoductDescription}
                handleClosePopup={this.hidePopup}
              />
    }

    return (
      <main className="womens-page">
        <Header />
        <button onClick={this.filterShoes}>Filter Shoes</button>
        <button onClick={this.removeFilters}>Remove</button>
        <ul className="list-of-products">
          {listOfProducts}
        </ul>
        {popup}
      </main>
    );
  }
}

export default Womens;
